import { StyledOptions } from "@emotion/styled"
import GatsbyLink from "gatsby-link"
import { OutboundLink } from "gatsby-plugin-google-analytics"
import { g, React } from "src/utils/view"

type Props = {
  href?: string
  to?: string
  newTab?: boolean
  children: React.ReactNode
}

export default function SiteLink({ href, to, children, newTab }: Props) {
  const finalDestination = to || href || "/"
  const isText =
    typeof children === "string" ||
    (typeof children === "object" && (children as string[]).length > 0)
  if (finalDestination.substr(0, 1) === "/" && newTab !== true) {
    return (
      <StyledLink
        isText={isText}
        activeStyle={{ color: "red" }}
        to={finalDestination}
      >
        {children}
      </StyledLink>
    )
  } else {
    return (
      <Link isText={isText} href={finalDestination} target={"_blank"}>
        {children}
      </Link>
    )
  }
}

const primaryLinkHighlightColor = "192, 98, 192"

type InnerLinkProps = {
  isText: boolean
}

const textLinkStyle = {
  backgroundImage: `linear-gradient( transparent 0%, transparent calc(40% - 8px), rgba(${primaryLinkHighlightColor}, 0.35) calc(40% - 8px), rgba(${primaryLinkHighlightColor}, 0.35) 120% )`,
  transition:
    "background-position 120ms ease-in-out, padding 120ms ease-in-out",
  backgroundSize: "100% 200%",
  backgroundPosition: "0 0",
  textDecoration: "none",
  color: "black",
  padding: 0,
  ":hover": {
    backgroundImage: `linear-gradient( transparent 0%, transparent calc(40% - 8px), rgba(${primaryLinkHighlightColor}, 1) calc(40% - 8px), rgba(${primaryLinkHighlightColor}, 1) 120% )`,
    backgroundPosition: "0 90%",
  },
}

const nonTextLinkStyle = {
  textDecoration: "none",
  color: "black",
  ":hover": {
    color: "red",
    padding: 0,
    outline: "1px solid gray",
    outlineOffset: 2,
  },
  ":focus": {
    outline: "1px dashed gray",
    outlineOffset: 2,
  },
  ":visited": {
    color: "#000",
    outlineOffset: 2,
  },
}

const linkStyle = ({ isText }: InnerLinkProps) =>
  isText ? textLinkStyle : nonTextLinkStyle

const options: StyledOptions = { shouldForwardProp: p => p !== "isText" }

const Link = g(OutboundLink, options)<
  { href: string; target: string } & InnerLinkProps
>(linkStyle)

const StyledLink = g(GatsbyLink, options)<InnerLinkProps>(linkStyle)
